import React from "react"
import { useState, useEffect } from "react"
import { Link, useNavigate } from "react-router-dom"
import {
  getAllUsers, // Import funkce pro zskn vech uivatel
  createUser, // Import funkce pro vytvoen novho uivatele
  deleteUser, // Import funkce pro smazn uivatele
} from "../../Services/UsersService/UsersService"
import AddModal from "../../Components/UsersModals/AddModal/AddModal" // Import komponenty pro modal pro pidn uivatele
// import EditModal from "../../Components/UsersModals/EditModal/EditModal" // Import komponenty pro modal pro pravu uivatele
import DeleteModal from "../../Components/UsersModals/DeleteModal/DeleteModal" // Import komponenty pro modal pro smazn uivatele

const Users = () => {
  const [users, setUsers] = useState([]) // Stav pro seznam uivatel
  const [showCreateModal, setShowCreateModal] = useState(false) // Stav pro zobrazen modal pro pidn uivatele
  const [showDeleteModal, setShowDeleteModal] = useState(false) // Stav pro zobrazen modal pro smazn uivatele
  const [userToDelete, setUserToDelete] = useState(null) // Stav pro uivatele k smazn

  // useEffect hook, kter se spust pi naten komponenty a zsk vechny uivatele
  useEffect(() => {
    const fetchUsers = async () => {
      const fetchedUsers = await getAllUsers() // Nate vechny uivatele
      setUsers(fetchedUsers) // Nastav seznam uivatel do stavu
    }
    fetchUsers() // Zavol funkci pro naten uivatel pi naten komponenty
  }, [])

  const navigate = useNavigate()

  // Funkce pro oteven modalu pro pidn uivatele
  const handleOpenCreateModal = () => setShowCreateModal(true)
  // Funkce pro zaven modalu pro pidn uivatele
  const handleCloseCreateModal = () => setShowCreateModal(false)
  // Funkce pro oteven modalu pro smazn uivatele
  const handleOpenDeleteModal = (userId) => {
    setShowDeleteModal(true) // Oteve modal pro smazn
    setUserToDelete(userId) // Nastav ID uivatele k smazn
  }
  // Funkce pro zaven modalu pro smazn uivatele
  const handleCloseDeleteModal = () => setShowDeleteModal(false)

  // Funkce pro vytvoen novho uivatele
  const handleCreateUser = async (userData) => {
    const result = await createUser(userData) // Zavol API pro vytvoen uivatele
    if (result.success) {
      setUsers([...users, result.user]) // Pid novho uivatele do seznamu
      handleCloseCreateModal() // Zave modal pro pidn uivatele
    } else {
      console.error("Chyba pi vytven uivatele:", result)
    }
  }

  // Funkce pro smazn uivatele
  const handleDeleteUser = async (userId) => {
    const result = await deleteUser(userId) // Zavol API pro smazn uivatele
    if (result.success) {
      setUsers(users.filter((user) => user.id !== userId)) // dynamicke zobrazeni zmenenych dat bez znovunacteni stranky
      handleCloseDeleteModal() // Zave modal pro smazn uivatele
      // window.location.reload()
      // navigate(0) // Obnov stranu
    } else {
      console.error("Chyba pi mazn uivatele:", result)
      // window.location.reload()
      navigate(0) // Obnov stranu
    }
  }

  return (
    <div>
      <div className="container">
        <h1 className="text-center text-success display-4 mt-4">Users</h1>
        <button
          type="button"
          className="btn btn-success"
          onClick={handleOpenCreateModal} // Oteve modal pro pidn uivatele
        >
           Add User
        </button>
        <div className="row row-cols-1 row-cols-md-3 g-4 mt-3">
          {users.map((user) => (
            <div key={user.id} className="col">
              <div className="card border-primary mb-3 shadow-sm w-100 h-100">
                <h5 className="card-header bg-transparent border-primary">
                  Info
                </h5>
                <div className="card-body">
                  <h4 className="card-title text-success">{user.userName}</h4>
                  <p className="card-text fst-italic">
                    <span>ID: {user.id}</span>
                    <br />
                    Name: {user.userName}
                    <br />
                    Email: {user.email}
                    <br />
                  </p>
                </div>
                <div className="card-footer bg-transparent border-primary">
                  <div className="row">
                    <div className="col">
                      <Link
                        to={`/users/edit/${user.id}`}
                        type="button"
                        className="btn btn-warning w-100 rounded-1"
                      >
                        Edit
                      </Link>
                    </div>
                    <div className="col">
                      <button
                        className="btn btn-danger w-100 rounded-1"
                        onClick={() => handleOpenDeleteModal(user.id)} // Oteve modal pro smazn tohoto uivatele
                      >
                        Delete
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Modal pro pidn uivatele */}
      <AddModal
        showModal={showCreateModal}
        setShowModal={setShowCreateModal}
        handleCreateUser={handleCreateUser}
      />

      {/* Modal pro smazn uivatele */}
      <DeleteModal
        showModal={showDeleteModal}
        setShowModal={setShowDeleteModal}
        handleCloseModal={handleCloseDeleteModal}
        handleDeleteUser={handleDeleteUser}
        userId={userToDelete}
      />
    </div>
  )
}

export default Users
