import React, { useState } from "react"
import { getObservationsByMultipleFilters } from "../../Services/WildlifeReserveService/WildlifeReserveService" // Importovn sluby pro zskn pozorovn s vcero filtry
import SearchBody from "../../Components/SearchBody/SearchBody" // Importovn komponenty pro zobrazen vsledk vyhledvn

// Komponenta pro zadn vyhledvacho vstupu a zobrazen vsledk
const SearchInput = ({ onSearch }) => {
  // Stav pro pozorovn, celkov poet vsledk a vstupn data
  const [observations, setObservations] = useState([]) // Stav pro uloen pozorovn
  const [totalResults, setTotalResults] = useState(0) // Stav pro uloen celkovho potu vsledk
  const [inputData, setInputData] = useState({
    taxonName: "", // Filtr pro nzev taxonu
    year: null, // Filtr pro rok
    month: null, // Filtr pro msc
    day: null, // Filtr pro den
    placeGuess: "", // Filtr pro msto
  })

  // Funkce pro zskn pozorovn na zklad filtr
  const fetchObservations = async () => {
    // Pokud nejsou zadny dn filtry, zobraz se chybov hlka
    if (
      !inputData.taxonName &&
      !inputData.year &&
      !inputData.month &&
      !inputData.day
    ) {
      console.error("At least one filter is required.") // Chybov zprva, e alespo jeden filtr je poadovn
      return
    }

    try {
      // Zavoln sluby pro zskn pozorovn s aktuln zadanmi filtry
      const response = await getObservationsByMultipleFilters(inputData)
      const fetchedObservations = response.results // Uloen vsledk do promnn
      setTotalResults(response.totalResults) // Nastaven celkovho potu vsledk
      // Uloen zskanch pozorovn do stavu, pokud jsou data platn
      setObservations(
        Array.isArray(fetchedObservations) ? fetchedObservations : [] // Zajitn, e data jsou ve form pole
      )
    } catch (error) {
      // Chyba pi zskvn pozorovn
      console.error("Error fetching observations:", error) // Chybov zprva
      setTotalResults(0) // Resetovn celkovho potu vsledk
      setObservations([]) // Resetovn seznamu pozorovn
    }
  }

  // Funkce pro vyhledvn na zklad aktulnho vstupu
  const handleSearch = () => {
    fetchObservations() // Zavoln funkce pro zskn pozorovn
    onSearch(inputData) // Voln funkce pedan jako prop pro informovn rodie (pokud je poteba)
  }

  // Funkce pro zmnu hodnoty vstupnho pole
  const handleInputChange = (e) => {
    const { name, value } = e.target
    setInputData({
      ...inputData,
      [name]: value,
    })
  }

  // Renderovn komponenty
  return (
    <div className="container">
      <div className="row justify-content-center align-items-center mt-3">
        <div className="col form-group d-flex flex-column gap-2">
          <div>
            <input
              className="form-control"
              type="text"
              name="taxonName"
              value={inputData.taxonName}
              onChange={handleInputChange}
              placeholder="Type common name (multiply filter)"
            />
          </div>
          <div>
            <input
              className="form-control"
              type="number"
              name="year"
              value={inputData.year}
              onChange={handleInputChange}
              placeholder="Type at least year"
            />
          </div>
          <div>
            <input
              className="form-control"
              type="number"
              name="month"
              value={inputData.month}
              onChange={handleInputChange}
              placeholder="Type month"
              disabled={!inputData.year}
            />
          </div>
          <div>
            <input
              className="form-control"
              type="number"
              name="day"
              value={inputData.day}
              onChange={handleInputChange}
              placeholder="Type day"
              disabled={!inputData.year || !inputData.month}
            />
          </div>
          <div>
            <input
              className="form-control"
              type="text"
              name="placeGuess"
              value={inputData.placeGuess}
              onChange={handleInputChange}
              placeholder="Type place guess (coming soon)"
              disabled
            />
          </div>
          <div>
            <button
              className="form-control btn btn-success"
              onClick={handleSearch}
            >
              Search
            </button>
          </div>
        </div>
        <hr className="mt-4" />
        <p className="fs-4 mt-1">
          Number of observations: {totalResults || "..."}
        </p>
      </div>
      <SearchBody observations={observations} totalResults={totalResults} />
    </div>
  )
}

export default SearchInput
